body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

.mobile-menu {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 320px;
  min-height: 100vh;
  box-shadow: 16px 0px 52px 0px #0e185033, -2px 0px 4px 0px #454f7e05; }

.mobile-header {
  min-height: 80px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center; }
  .mobile-header__button--close {
    background-image: url(./static/close.svg); }
  .mobile-header__button--search {
    background-image: url(./static/search.svg); }
  .mobile-header__logo {
    position: relative;
    left: -25px;
    width: 97px;
    height: 51px;
    object-fit: contain; }

.navigator {
  flex-grow: 0.5; }

.mobile-nav {
  display: flex;
  flex-direction: column;
  gap: 24px; }
  .mobile-nav__item {
    color: #1b1c21;
    height: 32px;
    padding-left: 20px;
    display: inline-block;
    line-height: 32px;
    font-weight: 500; }
  .mobile-nav__item:hover {
    padding-left: 16px;
    border-left: 4px solid #41f6d7;
    transition: border-left 0.6s;
    color: #7e7e82; }

.mobile-footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 20px; }

.mobile-contact {
  display: flex;
  flex-direction: row;
  gap: 16px; }
  .mobile-contact__button--phone {
    background-image: url(./static/call.svg); }
  .mobile-contact__button--chat {
    background-image: url(./static/chat.svg); }
  .mobile-contact__button--profile {
    background-image: url(./static/profile.svg); }

.mobile-adress {
  display: flex;
  flex-direction: column;
  font-style: normal;
  gap: 12px; }
  .mobile-adress__mail {
    color: black;
    font-size: 16px;
    line-height: 24px; }
  .mobile-adress__tel {
    color: black;
    font-size: 24px;
    line-height: 32px;
    font-weight: bold; }

.mobile-lang {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 7px; }
  .mobile-lang li {
    font-size: 16px;
    line-height: 24px; }
  .mobile-lang li:hover {
    color: #7e7e82; }

/* -------------MODAL-BURGER-------------*/
.modal-burger {
  max-width: 320px;
  min-height: 763px;
  float: left;
  border: none;
  overflow: hidden;
  position: sticky;
  padding: 0;
  margin: 0;
  box-shadow: 16px 0px 52px 0px #0e185033, -2px 0px 4px 0px #454f7e05; }

@media (min-width: 1440px) {
  .modal-burger {
    display: block;
    width: 320px;
    box-shadow: none; }
  .mobile-header__button--close {
    display: none; } }
