body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

.header {
  min-height: 88px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center; }
  .header-start {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 16px; }
  .header-end {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 16px; }
  @media (min-width: 1120px) {
    .header {
      min-height: 18px;
      visibility: hidden; } }
  @media (min-width: 768px) {
    .header {
      justify-content: space-between; }
      .header-start {
        margin-left: 24px;
        gap: 42px; }
      .header-end {
        margin-right: 24px; } }

.button {
  width: 40px;
  height: 40px;
  padding: 0;
  border: 0;
  background-color: transparent; }
  .button:hover {
    transition: opacity, transform 0.6s;
    transform: scale(110%);
    opacity: 0.5; }

.header__logo {
  width: 97px;
  height: 51px;
  object-fit: contain; }

.header__button--phone, .header__button--chat, .header__button--profile {
  display: none; }

.header__button--phone {
  background-image: url(./static/call.svg); }

.header__button--chat {
  background-image: url(./static/chat.svg); }

.header__button--profile {
  background-image: url(./static/profile.svg); }

.header__button--burger {
  background-image: url(./static/burger.svg); }

.header__button--repair {
  background-image: url(./static/repair.svg); }

.header__button--checkstatus {
  background-image: url(./static/checkstatus.svg); }

.header__button--repair::before {
  margin-left: -20px;
  content: '';
  width: 2px;
  height: 32px;
  background-color: #eaeaea;
  display: block; }

@media (min-width: 768px) {
  .header__button--phone, .header__button--chat, .header__button--profile {
    display: block; }
  .header__button--profile {
    margin-right: 13px; }
  .header__button--repair {
    margin-left: 13px; }
  .header__button--burger::after {
    margin-left: 60px;
    content: '';
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    display: block; } }
