body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

.swiper-list--services > .swiper-list__item--services {
  height: 160px; }

.swiper-list__item--services > .swiper-list__link {
  color: #1b1c21;
  display: flex;
  height: 130px;
  justify-content: space-between;
  align-items: flex-start; }

.swiper-list__item--services > .swiper-list__link::after {
  content: '';
  background-image: url(./static/go.svg);
  width: 40px;
  height: 40px;
  align-self: end; }

@media (min-width: 768px) {
  .swiper-list--services {
    grid-auto-rows: 160px; }
  .swiper-list > .swiper-list__item--services {
    display: none; }
  .swiper-list__item--services:nth-child(-n + 3) {
    display: block; } }

@media (min-width: 1120px) {
  .swiper-list__item--services:nth-child(-n + 4) {
    display: block; } }
