body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

dialog::-webkit-backdrop {
  background-color: #ffffff;
  opacity: 0.96; }

dialog::backdrop {
  background-color: #ffffff;
  opacity: 0.96; }

.modal-feedback,
.modal-phone {
  overflow: visible; }

.menu-modal {
  width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 24px 16px;
  background-color: #ffffff; }
  @media (min-width: 768px) {
    .menu-modal {
      height: 100vh;
      width: 440px;
      position: fixed;
      top: 0;
      right: 0;
      box-shadow: 16px 0px 52px 0px #0e185033, -2px 0px 4px 0px #454f7e05;
      padding: 40px 32px; } }

.menu-modal__button-close {
  background-image: url(./static/close.svg); }
  @media (min-width: 768px) {
    .menu-modal__button-close {
      position: absolute;
      left: -65px;
      background-image: url(./static/close.svg); } }

.menu-modal__title {
  position: relative; }
  .menu-modal__title::after {
    content: '';
    position: absolute;
    left: -16px;
    width: 4px;
    height: 40px;
    background-color: #41f6d7;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px; }
    @media (min-width: 768px) {
      .menu-modal__title::after {
        content: '';
        position: absolute;
        left: -32px; } }

.menu-modal__form > input {
  min-width: 100%;
  height: 48px;
  border-radius: 8px;
  border: 1px solid #eaeaea;
  padding-left: 10px;
  box-sizing: border-box; }

.menu-modal__form > textarea {
  min-width: 100%;
  height: 118px;
  border-radius: 8px;
  padding-left: 10px;
  border: 1px solid #eaeaea; }

.menu-modal__button-submit {
  width: 136px;
  height: 32px;
  border: none;
  background-color: transparent;
  background-image: url(./static/submit.svg);
  align-self: flex-end; }

.menu-modal__text {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px; }
  .menu-modal__text > a {
    color: #ff3e79;
    cursor: pointer; }
