body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

.swiper-header--price {
  height: 56px; }

.swiper-list--price > .swiper-list__item--price {
  width: 260px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 14px; }

.swiper-slide__order {
  position: absolute;
  width: 119px;
  height: 32px;
  background-image: url(./static/zakaz.svg);
  right: 15px;
  top: 150px; }

.swiper-slide__service::before {
  font-size: 14px;
  color: #7e7e82;
  display: block;
  content: 'Ремонтные услуги'; }

.swiper-slide__price::before {
  color: #7e7e82;
  display: block;
  content: 'Цена'; }

.swiper-slide__time::before {
  color: #7e7e82;
  display: block;
  content: 'Срок'; }

.swiper-list__item--price:first-child {
  display: none; }

.price-description {
  padding: 16px;
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px; }
  .price-description__link {
    display: block;
    margin-top: 16px;
    font-size: 16px;
    line-height: 26px;
    text-align: left;
    font-weight: 500; }
    .price-description__link:hover {
      cursor: pointer; }
    .price-description__link::after {
      content: '';
      background-image: url(./static/more.svg);
      width: 24px;
      height: 24px;
      position: absolute; }

@media (min-width: 768px) {
  .swiper-list--price {
    margin-left: 0;
    padding-top: 0px;
    display: grid !important;
    grid-template-columns: repeat(1, 720px) !important;
    grid-auto-rows: 60px;
    justify-content: center;
    align-content: center;
    gap: 0; }
    .swiper-list--price > .swiper-list__item--price {
      width: auto;
      height: auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-size: 14px;
      align-items: center;
      border-radius: 0;
      border: none; }
  .swiper-slide__service {
    flex-basis: 53%; }
  .swiper-list__item--price:first-child {
    display: flex;
    background-color: transparent;
    border: none;
    align-items: baseline;
    color: #7e7e82; }
  .swiper-list__item--price:nth-child(2) {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px; }
  .swiper-list__item--price:nth-child(n + 2):after {
    content: '';
    width: 688px;
    height: 1px;
    background-color: #eaeaea;
    position: absolute;
    bottom: 0px; }
  .swiper-list__item--price:last-child::after {
    display: none; }
  .swiper-list__item--price:last-child {
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px; }
  .swiper-list__item--price:hover {
    transition: all 0.6s ease-in-out;
    transform: scale(1);
    background-color: none;
    cursor: pointer; }
  .swiper-slide__service::before, .swiper-slide__price::before, .swiper-slide__time::before {
    display: none; }
  .swiper-slide__price, .swiper-slide__time {
    flex-basis: 15%; }
  .swiper-slide__order {
    position: static;
    width: 119px;
    height: 32px;
    background-image: url(./static/zakaz.svg);
    right: 0px;
    top: 0px; }
    .swiper-slide__order:hover {
      transition: opacity, transform 0.6s;
      transform: scale(110%);
      opacity: 0.5; }
  .swiper-slide__no-order {
    position: static;
    width: 119px;
    height: 32px;
    background-image: url(./static/zakaz.svg);
    right: 0px;
    top: 0px;
    visibility: hidden; }
  .price-description {
    padding: 24px;
    width: 63%; } }

@media (min-width: 1120px) {
  .swiper-list--price {
    grid-template-columns: repeat(1, 1056px) !important; }
    .swiper-list--price:nth-child(n + 2):after {
      content: '';
      width: 1008px;
      height: 1px;
      background-color: #eaeaea;
      position: absolute;
      bottom: 0px; }
    .swiper-list--price:last-child::after {
      display: none; }
  .price-description {
    padding: 32px;
    width: 47%; } }
