body {
  padding: 0;
  margin: 0;
  font-family: 'TT Lakes';
  color: #1b1c21; }

.Maket {
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100vh; }

.title__h1 {
  line-height: 40px;
  font-size: 28px;
  padding-left: 18px;
  margin-top: 24px;
  border-left: 4px solid #41f6d7; }

.title > span {
  display: none; }

.nav-list {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  overflow-x: auto; }
  .nav-list::-webkit-scrollbar {
    display: none; }
  .nav-list .nav-list__item {
    justify-content: center;
    white-space: nowrap;
    list-style-type: none;
    padding: 8px;
    padding-left: 16px;
    border: 4px solid transparent;
    font-size: 16px; }
    .nav-list .nav-list__item:first-child {
      margin-left: 16px; }
    .nav-list .nav-list__item:hover {
      border-radius: 5px;
      border: 4px solid #b8ffec;
      background-color: #ffffff;
      cursor: pointer; }
    .nav-list .nav-list__item > a {
      text-decoration: none;
      color: #7e7e82; }

.main {
  display: flex;
  flex-direction: column;
  padding: 16px;
  gap: 16px; }
  .main__foto {
    object-fit: contain; }
  @media (min-width: 768px) {
    .main {
      flex-direction: row; }
      .main > * {
        width: 50%; } }

.main__text > p {
  font-size: 14px;
  height: 111px;
  margin-bottom: 16px;
  white-space: pre-line;
  overflow: hidden; }

.main__text--all > p {
  height: 100% !important;
  overflow: visible; }

.main__text--all > .main__button {
  display: none; }

@media (min-width: 768px) {
  .main__text > p {
    height: 55%; } }

.main__button {
  border: none;
  background-color: transparent; }
  .main__button::before {
    width: 24px;
    height: 24px;
    content: '';
    background: url(./static/expand.svg);
    display: inline-block;
    vertical-align: middle; }

@media (min-width: 1120px) {
  .title {
    display: flex;
    justify-content: space-between; }
    .title__button--repair {
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
      background-image: url(./static/repair.svg); }
    .title__button--checkstatus {
      display: inline-block;
      vertical-align: middle;
      margin-left: 10px;
      background-image: url(./static/checkstatus.svg); }
  .title__h1 {
    flex-grow: 1; }
  .title > span {
    display: block;
    width: 200px;
    font-size: 16px; }
  .nav-list {
    flex-wrap: wrap; }
  .main__text > p {
    height: 75%; }
  .main__button {
    display: none; } }
